import React from "react";

import { CssBaseline, Fab, SvgIcon, colors } from "@mui/material";
import { ThemeProvider, createTheme } from "@mui/material/styles";

// ✅ 아이콘 컴포넌트 (기존 BrushIcon 유지)
const BrushIcon = (props: any) => (
  <SvgIcon {...props}>
    {/* materialdesignicons.com "arrow-right-circle" */}
    <path d="M22,12A10,10 0 0,1 12,22A10,10 0 0,1 2,12A10,10 0 0,1 12,2A10,10 0 0,1 22,12M6,13H14L10.5,16.5L11.92,17.92L17.84,12L11.92,6.08L10.5,7.5L14,11H6V13Z" />
  </SvgIcon>
);

type FloatingButtonProps = {
  onClick?: () => void;
  description: React.ReactNode;
};

const FloatingButton: React.FC<FloatingButtonProps> = ({
  onClick,
  description,
}) => {
  return (
    <Fab
      color="secondary"
      variant="extended"
      onClick={onClick}
      sx={(theme) => ({
        position: "absolute",
        top: theme.spacing(2),
        right: theme.spacing(2),
        zIndex: 2,
      })}
    >
      {description}
      <BrushIcon sx={{ ml: "0.75em" }} fontSize="inherit" />
    </Fab>
  );
};

// ✅ Create a theme instance.
const theme = createTheme({
  palette: {
    primary: { main: "#464766" },
    secondary: { main: "#AD50FF" },
    error: { main: colors.red.A400 },
    background: { default: "#fff" },
  },
  typography: {
    fontFamily: ["Nunito", "Roboto", "Arial", "sans-serif"].join(","),
  },
  components: {
    // MUI v5+에서는 props 대신 components.defaultProps 권장
    MuiButtonBase: {
      defaultProps: {
        disableRipple: false,
      },
    },
  },
});

type MaterialUIAppProps = {
  children: React.ReactNode;
};

const MaterialUIApp: React.FC<MaterialUIAppProps> = ({ children }) => (
  <ThemeProvider theme={theme}>
    <CssBaseline />
    {children}
  </ThemeProvider>
);

export { FloatingButton, MaterialUIApp };